$nav-z-base: 200;

body {
  padding-top: 100px;

  @include breakpoint($sm) {
    padding-top: 80px;
  }
}

#header {
  border-bottom: 1px solid $gray-lighter;
  background: #FFF;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: $nav-z-base;
}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  padding-left: 0px;
  padding-right: 0px;
  background: #FFF;
  padding-bottom: 0px;
}

.nav .brand {
  @extend .col-2;

  @extend .col-md-5;

  margin-bottom: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  color: $brand-primary;
}

.nav .title {
  display: none;

  @include breakpoint($lg) {
    display: block;
  }


  font-family: $trade-gothic;
  text-transform: uppercase;
  font-size: 1.4rem;
  letter-spacing: 1.5px;
}

// .nav .title.title-mobile
//   display: block
//   +breakpoint($md)
//     display: none

.nav img {
  max-height: 35px;
  margin-right: 20px;
}

.nav-primary {
  @extend .col;

  @extend .col-md-5;

  left: 0;
  right: 0;
  opacity: 1;
  position: fixed;
  top: 55px;
  background: white;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;

  @include breakpoint($sm) {
    top: 50%;
    transform: translateY(-50%);
    height: 100%;
    opacity: 0;
    position: absolute;
    margin-left: 80px;
    left: 10px;
    padding-left: 10px;
    background: white;
    display: flex;
    justify-content: flex-start;
    z-index: $nav-z-base + 2;
  }
}

#header.active .nav .nav-primary {
  opacity: 1;
  transition: all 0.25s ease;
}

.nav-primary ul {
  height: 100%;
  padding: 0px;
}

.nav-primary li {
  margin-right: 20px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-top: 10px;
  padding-bottom: 10px;

  &:after {
    content: "";
    position: absolute;
    height: 4px;
    width: 100%;
    opacity: 0;
    background: $brand-primary;
    left: 0;
    bottom: 0;
  }
}

.nav-primary li:hover {
  &:after {
    width: 100%;
    opacity: 1;
    transition: all 0.25s ease;
  }
}

.nav-primary .current-menu-item::after {
  width: 100%;
  opacity: 1;
}

.nav-primary li:last-of-type {
  margin-right: 0px;
}

.nav-primary li a {
  font-family: $trade-gothic;
  font-size: 16px;
  color: rgba(black, 0.2);
  letter-spacing: 2px;
  text-transform: uppercase;
  font-weight: bold;
  position: relative;

  &:hover {
    color: $brand-primary;
    transition: all 0.25s ease;
  }

  // &:after
  //   content: ""
  //   position: absolute
  //   height: 4px
  //   width: 0%
  //   background: $brand-primary
  //   left: 0
  //   bottom: -14px
  //   +breakpoint($sm)
  //     bottom: -18px
}

.nav-primary .current-menu-item a {
  color: $brand-primary;

  &:after {
    width: 100%;
  }
}

.nav-secondary {
  @extend .col-9;

  @extend .col-md-5;

  text-align: right;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  padding-bottom: 10px;
}

.nav-secondary .btn-info {
  // margin-bottom: 7px
}

.nav-secondary .vertical-line {
  position: relative;
  display: block;

  // margin-bottom: 10px
}

.nav-secondary .vertical-line {
  width: 2px;
  height: 32px;
  background: $gray-lighter;
  display: block;
  position: relative;
  margin-right: 10px;
}

.nav-secondary .open-search {
  display: none;
  background: transparent;
  color: $black;
  border: 2px solid $gray-lighter;

  // border: none
  margin-right: 10px;
  padding: 7px 12px;
}

.nav-secondary form {
  @extend .form-inline;

  text-align: right;
  flex-wrap: nowrap;
  justify-content: flex-end;
}

.nav-secondary.open form {
  input[type="search"] {
    width: 100%;
  }
}

.nav-secondary input {
  @extend .form-control;

  border-radius: 0px;
  border-color: transparent;
}

.nav-secondary input[type="submit"] {
  font-family: FontAwesome;
  color: $gray-light;
  cursor: pointer;
  width: 50px;

  &:hover, &:focus {
    color: $black;
  }

  // margin-bottom: 7px
}

.nav-secondary .form-group {
  margin-bottom: 0px;
}

.nav-secondary input[type="submit"]:focus {
  border: none;
}

.nav-secondary input[type="search"] {
  width: 20px;
  position: relative;
  top: 1px;
  padding-bottom: 10px;
  border: 0px solid transparent;
}

.nav-secondary.open input[type="search"] {
  background: $gray-lightest;
}

.nav-secondary input[type="search"]:focus {
  width: 100%;
  transition: all 0.25s ease;
  background: $gray-lightest;
}

.nav-secondary input[type="search"]:focus + .nav-secondary input[type="submit"] {
  color: $black;
}

