.screen-reader-text {
  display: none;
}

nav.navigation {
  text-align: right;
  margin-bottom: 60px;
}

.nav-links {
  @extend .row;

  display: flex;
  flex-direction: row;
  justify-content: space-between;

  div {
    @extend .col;
  }

  .nav-previous {
    @extend .col;

    text-align: right;
    order: 2;
  }

  .nav-next {
    text-align: left;
  }

  a {
    font-family: $cond;
    text-transform: uppercase;
    font-style: italic;
    letter-spacing: 1px;
    color: $gray-light;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      height: 2px;
      width: 100%;
      opacity: 0;
      background: $black;
      left: 0;
      bottom: -5px;
    }

    &:hover {
      color: $black;
      transition: all 0.25s ease;

      &:after {
        transition: all 0.25s ease;
        width: 100%;
        opacity: 1;
      }
    }
  }
}

footer.content-info {
  border-top: 1px solid $gray-lighter;
  padding-bottom: 5px;
  padding-top: 20px;
  background: $gray-lighter;
  margin-top: 40px;
  font-family: $trade-gothic;
  text-transform: uppercase;
}

footer.content-info .container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

footer.content-info ul.menu {
  list-style-type: none;
  padding-left: 0px;

  li {
    display: inline-block;
    margin-right: 10px;

    a {
      font-family: $trade-gothic;
      font-size: 16px;
      color: rgba(black, 0.2);
      letter-spacing: 2px;
      text-transform: uppercase;
      font-weight: bold;
      position: relative;

      &:hover {
        color: $brand-primary;
        transition: all 0.25s ease;
      }

      &:after {
        content: "";
        position: absolute;
        height: 4px;
        width: 0%;
        background: $brand-primary;
        left: 0;
        bottom: -21px;
      }
    }
  }
}



