.single .content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.single .entry-image {
  max-height: 50vh;
  overflow: hidden;
  margin-top: 40px;
  margin-bottom: 40px;

  img {
    height: 100%;
  }
}

.single article {
  @extend .col;

  @extend .col-md-12;

  @extend .col-lg-7;

  position: relative;
  margin-top: 20px;

  h1 {
    font-size: 55px;
  }

  img {
    max-width: 100%;
    height: auto;
  }

  img.alignright {
    float: right;
    margin-left: 10px;
    margin-bottom: 10px;
  }

  img.alignleft {
    float: left;
    margin-right: 10px;
    margin-bottom: 10px;
  }

  figure {
    max-width: 100%;
    height: auto;
  }

  figcaption {
    color: $gray-light;
    font-style: italic;
    margin-top: 0.5rem;
    text-align: center;
  }
}

.single blockquote {
  padding: 20px 25px;
  background: $gray-lightest;
  border-left: 8px solid $gray-light;

  p:last-of-type {
    margin-bottom: 0px;
  }
}

.single #sidebar {
  @extend .col;

  @extend .col-lg-4;

  display: none;
  padding-left: 0px;

  @include breakpoint($lg) {
    display: block;
  }


  @include breakpoint($xl) {
    padding-left: 30px;
  }


  margin-top: 50px;
  background: transparent;
  position: relative;

  // max-width: 350px
  &:before {
    content: "";
    width: 2px;
    height: 100%;
    position: absolute;
    left: -20px;
    top: 30px;
    background: $gray-lighter;
  }

  .section-title::after {
    max-width: 100%;
  }

  .book {
    background: transparent;
  }

  .widget_ninja_forms_widget {
    @extend #subscribe;

    // position: absolute
    margin-top: 100px;
    padding: 20px;

    // bottom: 30%
    max-width: 330px;

    &:before {
      height: 100px;
      bottom: -40px;
    }

    nf-fields-wrap {
      display: flex;
      flex-direction: row;
      width: 100%;
    }
  }
}

.single .sidebar-books {
  padding-top: 0px;
}

.single #sidebar .book {
  border: transparent;
  padding: 0px;
  width: 330px;
  display: flex;
  flex-direction: row;
  margin-bottom: 30px;

  .image-link {
    margin-right: 20px;

    img {
      width: 100px;
    }
  }

  .image-text {
    flex-grow: 2;
  }

  h4 {
    font-family: $font-family-sans-serif;
    text-transform: none;
    letter-spacing: 0px;
    font-size: 16px;
  }

  .book-banner {
    width: 155px;
    display: inline-block;
    padding: 10px 20px 10px 15px;
    font-size: 14px;
    font-family: $trade-gothic;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
}

.single #sidebar .widget_widget_crp {
  margin-top: 100px;
}

.single #sidebar, .single .entry-footer {
  .widget_widget_crp {
    ul {
      list-style-type: none;
      padding-left: 0px;
    }

    li {
      margin-bottom: 20px;
    }

    .crp_title {
      font-weight: bold;
      color: $black;
      display: block;

      &:hover {
        text-decoration: underline;
        text-decoration-color: $gray-light;
      }
    }

    .crp_author, .crp_date {
      color: $gray-light;
      font-family: $trade-gothic;
      text-transform: uppercase;
    }

    .crp_excerpt {
      display: block;
    }
  }
}

.single #sidebar, .single .entry-footer {
  .sidebar-title, .widget_widget_crp h3 {
    font-family: $trade-gothic;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 20px;
    margin-bottom: 20px;
    position: relative;
    display: inline-block;
    line-height: 2.2;

    &:after {
      position: absolute;
      content: "";
      width: 80%;
      bottom: 0;
      left: 0;
      height: 2px;
      background: $brand-info;
    }
  }
}

.single .entry-social {
  position: fixed;
  top: 140px;
  left: 10px;
  display: flex;
  flex-direction: column;
  margin-right: 24px;

  a {
    background: $gray;
    width: 30px;
    height: 30px;
    border-radius: 0px;
    color: white;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;

    &:hover {
      background: darken($brand-info, 10%);
    }
  }
}

.single .entry-footer .widget_widget_crp ul {
  @extend .row;

  li {
    @extend .col-6;
  }
}

.single .entry-footer {
  background: white;
  border-top: 0px solid transparent;
  margin-top: 20px;

  .skeptical-divider {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  }

  .skeptical-divider:before, .skeptical-divider::after {
    position: relative;
    content: "";
    width: 20%;
    height: 3px;
    background: $black;
  }

  .skeptical-divider img {
    height: 30px;
    margin: 10px;
  }
}



